/* ============================================
   YELLOW SHARE — LEGO-Inspired Design System
   ============================================ */

@import url('https://fonts.googleapis.com/css2?family=Space+Grotesk:wght@300;400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:wght,FILL@100..700,0..1&display=swap');

/* ---------- CSS Custom Properties ---------- */
:root {
  --primary: #f2cc0d;
  --primary-dark: #dcb700;
  --bg-light: #f8f8f5;
  --bg-dark: #221f10;
  --neutral-dark: #181711;
  --neutral-medium: #8a8360;
  --surface: #ffffff;
  --danger: #ef4444;
  --success: #22c55e;

  --shadow-lego: 4px 4px 0px 0px rgba(24, 23, 17, 1);
  --shadow-lego-sm: 2px 2px 0px 0px rgba(24, 23, 17, 1);
  --shadow-lego-lg: 8px 8px 0px 0px rgba(24, 23, 17, 1);

  --font-display: 'Space Grotesk', sans-serif;
  --border-thick: 4px solid var(--neutral-dark);
  --border-medium: 2px solid var(--neutral-dark);
  --radius-sm: 0.25rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --radius-xl: 1rem;
  --radius-full: 9999px;
}

/* ---------- Reset & Base ---------- */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: var(--font-display);
  background-color: var(--primary);
  color: var(--neutral-dark);
  min-height: 100vh;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

::selection {
  background: var(--neutral-dark);
  color: var(--primary);
}

a {
  color: inherit;
  text-decoration: none;
}

button {
  font-family: var(--font-display);
  cursor: pointer;
  border: none;
  background: none;
}

/* ---------- Material Symbols ---------- */
.material-symbols-outlined {
  font-variation-settings: 'FILL' 0, 'wght' 400, 'GRAD' 0, 'opsz' 24;
  vertical-align: middle;
}

/* ---------- Layout: App Shell ---------- */
.app-shell {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

/* ---------- Header ---------- */
.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  white-space: nowrap;
  border-bottom: var(--border-thick);
  background: var(--surface);
  padding: 16px 24px;
}

@media (min-width: 1024px) {
  .header {
    padding: 16px 48px;
  }
}

.header__brand {
  display: flex;
  align-items: center;
  gap: 12px;
}

.header__logo {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-sm);
  background: var(--primary);
  border: var(--border-medium);
  box-shadow: var(--shadow-lego-sm);
}

.header__logo .material-symbols-outlined {
  color: var(--neutral-dark);
  font-size: 24px;
}

.header__title {
  font-size: 1.25rem;
  font-weight: 700;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

.header__actions {
  display: flex;
  gap: 12px;
  align-items: center;
}

.header__nav {
  display: none;
  align-items: center;
  gap: 32px;
}

@media (min-width: 768px) {
  .header__nav {
    display: flex;
  }
}

.header__nav a {
  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.header__nav a:hover {
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 4px;
}

.icon-btn {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  border-radius: var(--radius-sm);
  background: var(--surface);
  border: var(--border-medium);
  box-shadow: var(--shadow-lego-sm);
  transition: all 0.15s ease;
}

.icon-btn:hover {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.status-pill {
  display: none;
  align-items: center;
  gap: 8px;
  background: var(--bg-light);
  padding: 4px 12px;
  border-radius: var(--radius-sm);
  border: var(--border-medium);
  font-size: 0.875rem;
  font-weight: 700;
}

@media (min-width: 768px) {
  .status-pill {
    display: flex;
  }
}

.status-pill__dot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--success);
  animation: pulse 2s infinite;
}

/* ---------- Main Content ---------- */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 24px;
  gap: 32px;
  max-width: 1600px;
  margin: 0 auto;
  width: 100%;
}

@media (min-width: 1024px) {
  .main-content {
    padding: 48px;
  }
}

/* ---------- Hero Section ---------- */
.hero {
  text-align: center;
  padding: 24px 0;
}

.hero__title {
  font-size: 2rem;
  font-weight: 900;
  letter-spacing: -0.02em;
  margin-bottom: 16px;
}

@media (min-width: 768px) {
  .hero__title {
    font-size: 3rem;
  }
}

@media (min-width: 1024px) {
  .hero__title {
    font-size: 3.75rem;
  }
}

.hero__subtitle {
  font-size: 1.125rem;
  font-weight: 500;
  max-width: 640px;
  margin: 0 auto;
  opacity: 0.8;
}

@media (min-width: 768px) {
  .hero__subtitle {
    font-size: 1.25rem;
  }
}

/* ---------- Grid Layout ---------- */
.content-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 32px;
}

@media (min-width: 1024px) {
  .content-grid {
    grid-template-columns: 2fr 1fr;
  }
}

/* ---------- Section Header ---------- */
.section-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
}

.section-title {
  font-size: 1.5rem;
  font-weight: 700;
  border-bottom: var(--border-thick);
  display: inline-block;
  padding-bottom: 4px;
}

.section-badge {
  background: var(--surface);
  border: var(--border-medium);
  padding: 4px 12px;
  border-radius: var(--radius-full);
  font-size: 0.875rem;
  font-weight: 700;
  box-shadow: var(--shadow-lego-sm);
}

/* ---------- Device Cards Grid ---------- */
.devices-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px;
}

@media (min-width: 640px) {
  .devices-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1280px) {
  .devices-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* ---------- Device Card ---------- */
.device-card {
  display: flex;
  flex-direction: column;
  background: var(--surface);
  border: var(--border-thick);
  border-radius: var(--radius-xl);
  padding: 16px;
  box-shadow: var(--shadow-lego);
  transition: all 0.15s ease;
  text-align: left;
  position: relative;
  overflow: hidden;
  width: 100%;
}

.device-card:hover {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.device-card__arrow {
  position: absolute;
  top: 8px;
  right: 8px;
  opacity: 0;
  transition: opacity 0.2s ease;
}

.device-card:hover .device-card__arrow {
  opacity: 1;
}

.device-card__image {
  width: 100%;
  aspect-ratio: 16 / 9;
  background: #f5f5f5;
  border-radius: var(--radius-sm);
  border: var(--border-medium);
  margin-bottom: 16px;
  overflow: hidden;
  position: relative;
}

.device-card__image-bg {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  filter: grayscale(100%);
  transition: filter 0.3s ease;
}

.device-card:hover .device-card__image-bg {
  filter: grayscale(0%);
}

.device-card__name {
  font-size: 1.125rem;
  font-weight: 700;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.device-card__info {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
}

.device-card__info .material-symbols-outlined {
  font-size: 18px;
}

.device-card__info span:last-child {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--neutral-medium);
}

/* ---------- Scanning Card ---------- */
.scanning-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: rgba(255, 255, 255, 0.5);
  border: 4px dashed rgba(24, 23, 17, 0.3);
  border-radius: var(--radius-xl);
  padding: 16px;
  min-height: 260px;
  animation: pulse 2s infinite;
}

.scanning-card__icon-wrap {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  border: 4px solid rgba(24, 23, 17, 0.3);
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 16px;
}

.scanning-card__icon-wrap .material-symbols-outlined {
  font-size: 2rem;
  color: rgba(24, 23, 17, 0.3);
  animation: spin 2s linear infinite;
}

.scanning-card__text {
  font-size: 1.125rem;
  font-weight: 700;
  color: rgba(24, 23, 17, 0.5);
}

/* ---------- File Drop Zone ---------- */
.drop-zone {
  flex: 1;
  background: var(--surface);
  border: 4px dashed var(--neutral-dark);
  border-radius: var(--radius-xl);
  padding: 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  gap: 24px;
  position: relative;
  transition: background-color 0.2s ease;
  box-shadow: var(--shadow-lego);
}

.drop-zone:hover {
  background: #fafafa;
}

.drop-zone--active {
  background: rgba(242, 204, 13, 0.1);
  border-color: var(--primary-dark);
}

/* Corner studs */
.drop-zone__stud {
  position: absolute;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: var(--border-medium);
  background: var(--primary);
}

.drop-zone__stud--tl {
  top: 16px;
  left: 16px;
}

.drop-zone__stud--tr {
  top: 16px;
  right: 16px;
}

.drop-zone__stud--bl {
  bottom: 16px;
  left: 16px;
}

.drop-zone__stud--br {
  bottom: 16px;
  right: 16px;
}

.drop-zone__icon {
  width: 96px;
  height: 96px;
  background: var(--primary);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: var(--border-thick);
  box-shadow: var(--shadow-lego-sm);
  transition: transform 0.2s ease;
}

.drop-zone:hover .drop-zone__icon {
  transform: scale(1.1);
}

.drop-zone__icon .material-symbols-outlined {
  font-size: 48px;
}

.drop-zone__title {
  font-size: 1.5rem;
  font-weight: 700;
}

.drop-zone__desc {
  color: var(--neutral-medium);
  font-weight: 500;
}

.drop-zone__divider {
  width: 100%;
  height: 1px;
  background: #e5e5e5;
}

.drop-zone__browse-btn {
  width: 100%;
  padding: 12px 24px;
  background: var(--neutral-dark);
  color: var(--surface);
  font-weight: 700;
  border-radius: var(--radius-sm);
  box-shadow: var(--shadow-lego-sm);
  transition: all 0.15s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  font-size: 1rem;
}

.drop-zone__browse-btn:hover {
  background: #333;
}

.drop-zone__browse-btn:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.drop-zone__limit {
  font-size: 0.75rem;
  color: var(--neutral-medium);
  font-weight: 700;
}

/* ---------- Footer ---------- */
.footer {
  margin-top: auto;
  border-top: var(--border-thick);
  background: var(--surface);
  padding: 24px 32px;
}

.footer__inner {
  max-width: 1600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}

@media (min-width: 768px) {
  .footer__inner {
    flex-direction: row;
  }
}

.footer__brand {
  display: flex;
  align-items: center;
  gap: 8px;
}

.footer__brand-dot {
  width: 12px;
  height: 12px;
  background: var(--primary);
  border: 1px solid var(--neutral-dark);
  border-radius: 50%;
}

.footer__brand span {
  font-weight: 700;
}

.footer__links {
  display: flex;
  align-items: center;
  gap: 32px;
}

.footer__links a {
  font-weight: 500;
  transition: color 0.15s;
}

.footer__links a:hover {
  color: var(--primary-dark);
  text-decoration: underline;
  text-decoration-thickness: 4px;
  text-underline-offset: 4px;
  text-decoration-color: var(--primary);
}

.footer__dots {
  display: flex;
  gap: 8px;
}

.footer__dots span {
  width: 16px;
  height: 16px;
  border-radius: 50%;
  border: 2px solid var(--neutral-dark);
}

/* ---------- Incoming Request Modal ---------- */
.incoming-overlay {
  position: fixed;
  inset: 0;
  z-index: 100;
  display: flex;
  align-items: center;
  justify-content: center;
  background: rgba(0, 0, 0, 0.4);
  backdrop-filter: blur(4px);
  animation: fadeIn 0.3s ease;
}

.incoming-card {
  width: 100%;
  max-width: 680px;
  margin: 16px;
  overflow: hidden;
  border-radius: var(--radius-xl);
  border: var(--border-thick);
  background: var(--surface);
  box-shadow: var(--shadow-lego);
  animation: slideIn 0.5s ease-out;
}

.incoming-card__strip {
  height: 12px;
  width: 100%;
  background: var(--neutral-dark);
}

.incoming-card__body {
  display: flex;
  flex-direction: column;
}

@media (min-width: 768px) {
  .incoming-card__body {
    flex-direction: row;
  }
}

.incoming-card__info {
  flex: 1;
  padding: 24px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.incoming-card__badges {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 8px;
}

.badge {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-sm);
  padding: 4px 8px;
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.badge--dark {
  background: var(--neutral-dark);
  color: var(--primary);
}

.badge--light {
  color: #9ca3af;
}

.incoming-card__title {
  font-size: 1.5rem;
  font-weight: 700;
  line-height: 1.2;
  margin-bottom: 4px;
}

.incoming-card__size {
  font-size: 1rem;
  font-weight: 500;
  color: #6b7280;
  margin-bottom: 24px;
}

.incoming-card__previews {
  display: flex;
  gap: 12px;
  margin-bottom: 24px;
}

.file-preview-icon {
  display: flex;
  width: 48px;
  height: 48px;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-sm);
  border: 2px solid #e5e7eb;
  background: #f9fafb;
}

.file-preview-icon .material-symbols-outlined {
  color: #6b7280;
}

.incoming-card__actions {
  display: flex;
  gap: 16px;
  margin-top: auto;
}

.btn {
  display: flex;
  height: 48px;
  align-items: center;
  justify-content: center;
  gap: 8px;
  border-radius: var(--radius-sm);
  border: var(--border-medium);
  font-size: 1rem;
  font-weight: 700;
  transition: all 0.15s ease;
  padding: 0 16px;
}

.btn--outline {
  flex: 1;
  background: var(--surface);
  color: var(--neutral-dark);
  box-shadow: var(--shadow-lego-sm);
}

.btn--outline:hover {
  background: #f9fafb;
  box-shadow: 3px 3px 0px 0px rgba(24, 23, 17, 1);
}

.btn--outline:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.btn--primary {
  flex: 2;
  background: var(--primary);
  color: var(--neutral-dark);
  box-shadow: var(--shadow-lego-sm);
}

.btn--primary:hover {
  filter: brightness(1.05);
  box-shadow: 3px 3px 0px 0px rgba(24, 23, 17, 1);
}

.btn--primary:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.btn .material-symbols-outlined {
  font-size: 20px;
}

.incoming-card__preview-panel {
  position: relative;
  min-height: 200px;
  background: #f3f4f6;
  border-top: var(--border-thick);
}

@media (min-width: 768px) {
  .incoming-card__preview-panel {
    width: 256px;
    border-top: none;
    border-left: var(--border-thick);
  }
}

.incoming-card__preview-bg {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
}

.incoming-card__preview-overlay {
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.1);
}

.incoming-card__sender {
  position: absolute;
  bottom: 16px;
  left: 16px;
  right: 16px;
  background: rgba(255, 255, 255, 0.9);
  backdrop-filter: blur(8px);
  border: var(--border-medium);
  padding: 12px;
  border-radius: var(--radius-sm);
}

.incoming-card__sender-inner {
  display: flex;
  align-items: center;
  gap: 12px;
}

.incoming-card__sender-icon {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: var(--primary);
  border: 1px solid var(--neutral-dark);
  display: flex;
  align-items: center;
  justify-content: center;
}

.incoming-card__sender-icon .material-symbols-outlined {
  font-size: 16px;
  font-weight: 700;
}

.incoming-card__sender-label {
  font-size: 0.75rem;
  font-weight: 700;
  color: #9ca3af;
  text-transform: uppercase;
}

.incoming-card__sender-name {
  font-size: 0.875rem;
  font-weight: 700;
}

/* ---------- Transfer Progress ---------- */
.transfer-page {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 16px;
  position: relative;
}

@media (min-width: 768px) {
  .transfer-page {
    padding: 32px;
  }
}

.transfer-page__bg {
  position: absolute;
  inset: 0;
  opacity: 0.1;
  pointer-events: none;
  background-image: radial-gradient(#e5e5e5 15%, transparent 16%), radial-gradient(#e5e5e5 15%, transparent 16%);
  background-size: 20px 20px;
  background-position: 0 0, 10px 10px;
}

.transfer-card {
  width: 100%;
  max-width: 960px;
  background: var(--bg-light);
  border-radius: var(--radius-xl);
  box-shadow: var(--shadow-lego-lg);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  min-height: 500px;
  border: var(--border-thick);
  z-index: 10;
}

@media (min-width: 768px) {
  .transfer-card {
    flex-direction: row;
  }
}

/* Left: Visualizer */
.transfer-card__viz {
  width: 100%;
  padding: 32px;
  background: var(--surface);
  position: relative;
  display: flex;
  flex-direction: column;
  border-bottom: var(--border-thick);
}

@media (min-width: 768px) {
  .transfer-card__viz {
    width: 50%;
    border-bottom: none;
    border-right: var(--border-thick);
  }
}

.transfer-card__viz-icon {
  color: rgba(24, 23, 17, 0.2);
}

.transfer-card__viz-icon .material-symbols-outlined {
  font-size: 2rem;
}

.transfer-viz-area {
  flex: 1;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 32px 16px;
}

.transfer-viz__stack {
  position: relative;
  width: 192px;
  height: 256px;
  border-bottom: 4px solid var(--neutral-dark);
  display: flex;
  flex-direction: column-reverse;
  gap: 4px;
  padding: 4px;
}

.transfer-viz__block {
  width: 100%;
  height: 32px;
  background: var(--primary);
  border: var(--border-medium);
  box-shadow: 2px 2px 0px rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

.transfer-viz__block--pulse {
  animation: pulse 2s infinite;
}

.transfer-viz__block--falling {
  position: absolute;
  top: -48px;
  left: 0;
  width: calc(100% - 8px);
  margin-left: 4px;
  transform: rotate(3deg);
  opacity: 0.8;
}

.transfer-card__viz-label {
  margin-top: 16px;
  text-align: center;
  font-size: 0.875rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  color: rgba(24, 23, 17, 0.4);
}

/* Right: Details */
.transfer-card__details {
  width: 100%;
  padding: 32px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: var(--bg-light);
}

@media (min-width: 768px) {
  .transfer-card__details {
    width: 50%;
    padding: 48px;
  }
}

.transfer-card__header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 8px;
}

.transfer-card__close {
  color: rgba(24, 23, 17, 0.4);
  transition: color 0.15s;
}

.transfer-card__close:hover {
  color: var(--danger);
}

.transfer-card__filename {
  font-size: 1.75rem;
  font-weight: 900;
  line-height: 1;
  margin-bottom: 8px;
  word-break: break-all;
}

@media (min-width: 768px) {
  .transfer-card__filename {
    font-size: 2rem;
  }
}

.transfer-card__recipient {
  font-size: 1.125rem;
  font-weight: 500;
  color: rgba(24, 23, 17, 0.6);
}

.transfer-card__metrics {
  display: flex;
  flex-direction: column;
  gap: 24px;
  margin: 32px 0;
}

.transfer-card__percent {
  display: flex;
  align-items: baseline;
  gap: 8px;
}

.transfer-card__percent-num {
  font-size: 4.5rem;
  font-weight: 900;
  line-height: 1;
}

.transfer-card__percent-sym {
  font-size: 2.5rem;
  font-weight: 700;
}

.transfer-card__progress {
  width: 100%;
  height: 16px;
  border: var(--border-medium);
  border-radius: var(--radius-full);
  padding: 2px;
  background: var(--surface);
}

.transfer-card__progress-fill {
  height: 100%;
  background: var(--primary);
  border-radius: var(--radius-full);
  transition: width 0.3s ease;
  border-right: 2px solid var(--neutral-dark);
  box-shadow: 2px 0 0 rgba(0, 0, 0, 0.1);
}

.transfer-card__stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

.stat-box {
  padding: 16px;
  background: var(--surface);
  border: 2px solid rgba(24, 23, 17, 0.05);
  border-radius: var(--radius-lg);
}

.stat-box__label {
  font-size: 0.75rem;
  text-transform: uppercase;
  font-weight: 700;
  color: rgba(24, 23, 17, 0.4);
  margin-bottom: 4px;
}

.stat-box__value {
  font-size: 1.125rem;
  font-weight: 700;
}

.transfer-card__btns {
  display: flex;
  gap: 16px;
}

.btn--transfer-outline {
  flex: 1;
  background: var(--surface);
  border: var(--border-medium);
  color: var(--neutral-dark);
  font-weight: 700;
  padding: 12px 24px;
  border-radius: var(--radius-sm);
  box-shadow: var(--shadow-lego);
  transition: all 0.15s ease;
  font-size: 1rem;
}

.btn--transfer-outline:hover {
  background: #f9fafb;
}

.btn--transfer-outline:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.btn--transfer-dark {
  flex: 1;
  background: var(--neutral-dark);
  color: var(--primary);
  border: var(--border-medium);
  font-weight: 700;
  padding: 12px 24px;
  border-radius: var(--radius-sm);
  box-shadow: 4px 4px 0px rgba(255, 255, 255, 0.5);
  transition: all 0.15s ease;
  font-size: 1rem;
}

.btn--transfer-dark:hover {
  background: #222;
}

.btn--transfer-dark:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.transfer-page__indicator {
  margin-top: 32px;
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.875rem;
  font-weight: 500;
  opacity: 0.8;
}

.transfer-page__indicator-dot {
  width: 8px;
  height: 8px;
  background: var(--neutral-dark);
  border-radius: 50%;
  animation: pulse 2s infinite;
}

/* ---------- Recent Transfers ---------- */
.recent-transfers {
  margin-top: 64px;
  width: 100%;
  max-width: 1024px;
}

.recent-transfers__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 24px;
}

.recent-transfers__title {
  font-size: 1.5rem;
  font-weight: 900;
}

.recent-transfers__link {
  font-size: 0.875rem;
  font-weight: 700;
  text-decoration: underline;
  text-decoration-thickness: 2px;
  text-underline-offset: 4px;
}

.recent-transfers__link:hover {
  opacity: 0.7;
}

.recent-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 24px;
}

@media (min-width: 640px) {
  .recent-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (min-width: 1024px) {
  .recent-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

.recent-card {
  border-radius: var(--radius-lg);
  border: var(--border-medium);
  background: var(--surface);
  padding: 16px;
  box-shadow: var(--shadow-lego-sm);
  transition: all 0.2s ease;
}

.recent-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lego);
}

.recent-card__top {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  margin-bottom: 16px;
}

.recent-card__icon {
  display: flex;
  width: 40px;
  height: 40px;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-sm);
  border: var(--border-medium);
}

.recent-card__icon--image {
  background: #dbeafe;
}

.recent-card__icon--doc {
  background: #fee2e2;
}

.recent-card__icon--video {
  background: #f3e8ff;
}

.recent-card__time {
  font-size: 0.75rem;
  font-weight: 700;
  color: #9ca3af;
}

.recent-card__name {
  font-size: 1.125rem;
  font-weight: 700;
  margin-bottom: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.recent-card__desc {
  font-size: 0.875rem;
  color: #6b7280;
  margin-bottom: 12px;
}

.recent-card__bar {
  height: 6px;
  width: 100%;
  border-radius: var(--radius-full);
  background: #f3f4f6;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.1);
}

.recent-card__bar-fill {
  height: 100%;
  width: 100%;
  background: var(--success);
}

/* ---------- Room Info ---------- */
.room-info {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-bottom: 4px;
}

.room-info__label {
  font-size: 0.875rem;
  font-weight: 500;
  opacity: 0.6;
}

.room-info__code {
  font-family: monospace;
  background: rgba(255, 255, 255, 0.2);
  padding: 2px 8px;
  border-radius: var(--radius-sm);
  font-weight: 700;
  font-size: 0.875rem;
}

/* ---------- Animations ---------- */
@keyframes pulse {

  0%,
  100% {
    opacity: 1;
  }

  50% {
    opacity: 0.6;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ---------- Hidden file input ---------- */
.hidden-input {
  display: none;
}

/* ---------- Avatar ---------- */
.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: var(--neutral-dark);
  color: var(--primary);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  border: var(--border-medium);
  font-size: 0.875rem;
}

/* ================================================
   MOBILE STYLES (≤768px)
   ================================================ */

/* Responsive visibility helpers */
.desktop-only {
  display: block;
}

.mobile-only {
  display: none !important;
}

@media (max-width: 768px) {
  .desktop-only {
    display: none !important;
  }

  .mobile-only {
    display: flex !important;
  }
}

/* ---------- Mobile Icon Button ---------- */
.mobile-icon-btn {
  display: flex;
  width: 48px;
  height: 48px;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-lg);
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  box-shadow: var(--shadow-lego-sm);
  transition: all 0.15s ease;
}

.mobile-icon-btn:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.mobile-icon-btn .material-symbols-outlined {
  font-size: 1.5rem;
  font-weight: 700;
}

/* ---------- Mobile Home ---------- */
.mobile-home {
  flex-direction: column;
  min-height: 100vh;
  background: var(--primary);
  overflow-x: hidden;
}

.mobile-home__header,
.mobile-waiting__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px 24px 8px;
}

.mobile-home__title,
.mobile-waiting__title {
  font-size: 1.5rem;
  font-weight: 700;
  letter-spacing: -0.02em;
  text-transform: uppercase;
  text-align: center;
  flex: 1;
}

.mobile-home__body {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 24px;
  align-items: center;
  gap: 32px;
  justify-content: center;
}

/* ---------- Mobile Send Area (Circular Button with Orbits) ---------- */
.mobile-send-area {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px;
}

.mobile-send-area__orbit {
  position: absolute;
  border-radius: 50%;
  border: 2px solid var(--neutral-dark);
}

.mobile-send-area__orbit--outer {
  width: 256px;
  height: 256px;
  opacity: 0.2;
  border-style: dashed;
  animation: spin 10s linear infinite;
}

.mobile-send-area__orbit--inner {
  width: 208px;
  height: 208px;
  opacity: 0.4;
}

.mobile-send-area__btn {
  position: relative;
  z-index: 10;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 176px;
  height: 176px;
  background: var(--surface);
  border: 3px solid var(--neutral-dark);
  box-shadow: var(--shadow-lego);
  border-radius: 50%;
  cursor: pointer;
  transition: all 0.15s ease;
}

.mobile-send-area__btn:hover {
  transform: scale(1.05);
}

.mobile-send-area__btn:active {
  transform: translate(4px, 4px) scale(0.95);
  box-shadow: none;
}

.mobile-send-area__label {
  font-size: 0.875rem;
  font-weight: 900;
  letter-spacing: 0.2em;
  margin-top: 4px;
}

/* ---------- Mobile Waiting Text ---------- */
.mobile-waiting__text {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  max-width: 480px;
}

.mobile-waiting__heading {
  font-size: 1.875rem;
  font-weight: 700;
  letter-spacing: -0.02em;
  text-align: center;
  text-transform: uppercase;
}

.mobile-waiting__desc {
  font-size: 0.875rem;
  font-weight: 500;
  text-align: center;
  opacity: 0.8;
  padding: 0 16px;
  line-height: 1.6;
  max-width: 280px;
}

/* ---------- Mobile Select Files Button ---------- */
.mobile-select-btn {
  display: flex;
  min-width: 200px;
  cursor: pointer;
  align-items: center;
  justify-content: center;
  gap: 12px;
  border-radius: var(--radius-xl);
  height: 56px;
  padding: 0 32px;
  background: var(--neutral-dark);
  color: var(--surface);
  border: 2px solid var(--neutral-dark);
  font-size: 1.125rem;
  font-weight: 700;
  transition: all 0.15s ease;
}

.mobile-select-btn:active {
  transform: scale(0.95);
}

/* ---------- Mobile Devices Card ---------- */
.mobile-devices-card {
  width: 100%;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(8px);
  border-radius: 1rem;
  padding: 24px;
  border: 2px solid var(--neutral-dark);
  box-shadow: var(--shadow-lego);
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.mobile-devices-card__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 8px;
}

.mobile-devices-card__header h3 {
  font-size: 1.125rem;
  font-weight: 700;
  text-transform: uppercase;
}

.mobile-devices-card__status {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: var(--primary);
  padding: 4px 12px;
  border-radius: var(--radius-full);
  border: 1px solid var(--neutral-dark);
  position: relative;
}

.mobile-devices-card__pulse {
  position: relative;
  width: 8px;
  height: 8px;
}

.mobile-devices-card__pulse::before {
  content: '';
  position: absolute;
  inset: 0;
  border-radius: 50%;
  background: var(--neutral-dark);
  animation: ping 1s infinite;
}

.mobile-devices-card__pulse-inner {
  position: absolute;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--neutral-dark);
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}

@keyframes ping {
  0% {
    transform: scale(1);
    opacity: 1;
  }

  75%,
  100% {
    transform: scale(2);
    opacity: 0;
  }
}

/* ---------- Mobile Device Row ---------- */
.mobile-device-row {
  display: flex;
  align-items: center;
  gap: 16px;
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  border-radius: var(--radius-xl);
  padding: 0 16px;
  height: 80px;
  box-shadow: var(--shadow-lego-sm);
  transition: transform 0.15s ease;
}

.mobile-device-row:hover {
  transform: translateY(-2px);
}

.mobile-device-row--offline {
  background: #f5f5f5;
  border: 2px dashed #d1d5db;
  box-shadow: none;
  opacity: 0.8;
}

.mobile-device-row__icon {
  display: flex;
  width: 48px;
  height: 48px;
  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-lg);
  border: 2px solid var(--neutral-dark);
  background: var(--primary);
}

.mobile-device-row__icon--offline {
  background: var(--surface);
  border-color: #d1d5db;
  color: #9ca3af;
}

.mobile-device-row__icon .material-symbols-outlined {
  font-size: 1.5rem;
}

.mobile-device-row__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex: 1;
}

.mobile-device-row__name {
  font-size: 1rem;
  font-weight: 700;
  line-height: 1;
  margin-bottom: 4px;
}

.mobile-device-row__desc {
  font-size: 0.75rem;
  font-weight: 600;
  opacity: 0.6;
}

.mobile-device-row__action {
  flex-shrink: 0;
  padding: 8px;
  border-radius: var(--radius-lg);
  border: 2px solid transparent;
  background: transparent;
  transition: all 0.15s ease;
  cursor: pointer;
}

.mobile-device-row__action:hover:not(:disabled) {
  background: var(--primary);
  border-color: var(--neutral-dark);
}

.mobile-device-row__action:disabled {
  color: #9ca3af;
  cursor: not-allowed;
}

/* ---------- Mobile Footer ---------- */
.mobile-footer {
  margin-top: auto;
  padding: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  border-top: 2px solid var(--neutral-dark);
  background: var(--surface);
}

.mobile-footer__network {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 0.875rem;
  font-weight: 700;
  background: var(--primary);
  padding: 6px 16px;
  border-radius: var(--radius-full);
  border: 1px solid var(--neutral-dark);
}

.mobile-footer__disc {
  font-size: 0.6875rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  opacity: 0.7;
  text-align: center;
  margin-top: 8px;
}

/* ---------- Mobile Transfer ---------- */
.mobile-transfer {
  flex-direction: column;
  min-height: 100vh;
  background: var(--primary);
  max-width: 448px;
  margin: 0 auto;
  border-left: 4px solid var(--neutral-dark);
  border-right: 4px solid var(--neutral-dark);
}

.mobile-transfer__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 24px;
  position: relative;
  z-index: 10;
}

.mobile-transfer__title {
  font-size: 1.25rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: -0.02em;
}

.mobile-transfer__body {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 16px 24px;
  align-items: center;
  justify-content: center;
  gap: 24px;
}

/* ---------- Circular Progress ---------- */
.circular-progress {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 256px;
  height: 256px;
}

.circular-progress__bg {
  position: absolute;
  width: 256px;
  height: 256px;
  border-radius: 50%;
  background: var(--surface);
  border: 4px solid var(--neutral-dark);
  box-shadow: var(--shadow-lego);
}

.circular-progress__svg {
  width: 256px;
  height: 256px;
  transform: rotate(-90deg);
  z-index: 10;
  filter: drop-shadow(1px 1px 2px rgba(0, 0, 0, 0.1));
}

.circular-progress__track {
  color: rgba(17, 17, 17, 0.1);
}

.circular-progress__fill {
  color: var(--surface);
}

.circular-progress__content {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 20;
  gap: 12px;
}

.mobile-transfer__file-icon {
  width: 96px;
  height: 96px;
  background: var(--primary);
  border: 2px solid var(--neutral-dark);
  box-shadow: var(--shadow-lego-sm);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-lg);
  overflow: hidden;
}

.mobile-transfer__file-icon .material-symbols-outlined {
  font-size: 48px;
}

.mobile-transfer__percent-badge {
  font-size: 2rem;
  font-weight: 900;
  letter-spacing: -0.04em;
  background: var(--surface);
  padding: 2px 8px;
  border: 2px solid var(--neutral-dark);
  box-shadow: var(--shadow-lego-sm);
  transform: rotate(-2deg);
}

/* ---------- Mobile Transfer Info Card ---------- */
.mobile-transfer__info-card {
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  padding: 16px;
  width: 100%;
  text-align: center;
  box-shadow: var(--shadow-lego);
  border-radius: var(--radius-lg);
}

.mobile-transfer__filename {
  font-size: 1.25rem;
  font-weight: 700;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0 8px;
}

.mobile-transfer__stats-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 12px;
  font-weight: 500;
  margin-top: 4px;
  font-size: 0.875rem;
}

.mobile-transfer__dot {
  width: 6px;
  height: 6px;
  background: var(--neutral-dark);
  border-radius: 50%;
}

/* ---------- Mobile Peer Card ---------- */
.mobile-transfer__peer-card {
  width: 100%;
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  border-radius: var(--radius-lg);
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 16px;
  box-shadow: var(--shadow-lego);
}

.mobile-transfer__peer-side {
  display: flex;
  align-items: center;
  gap: 12px;
}

.mobile-transfer__peer-side--right {
  flex-direction: row-reverse;
  text-align: right;
}

.mobile-transfer__peer-icon {
  width: 40px;
  height: 40px;
  border-radius: var(--radius-md);
  border: 2px solid var(--neutral-dark);
  display: flex;
  align-items: center;
  justify-content: center;
}

.mobile-transfer__peer-icon--sender {
  background: var(--primary);
  color: var(--neutral-dark);
}

.mobile-transfer__peer-icon--receiver {
  background: var(--neutral-dark);
  color: var(--surface);
}

.mobile-transfer__peer-icon .material-symbols-outlined {
  font-size: 1.25rem;
}

.mobile-transfer__peer-meta {
  display: flex;
  flex-direction: column;
}

.mobile-transfer__peer-label {
  font-size: 0.625rem;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  font-weight: 700;
  opacity: 0.6;
}

.mobile-transfer__peer-name {
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.2;
}

.mobile-transfer__peer-dots {
  display: flex;
  gap: 4px;
  align-items: center;
}

.dot {
  width: 6px;
  height: 6px;
  background: var(--neutral-dark);
  border-radius: 50%;
  animation: bounce 1s ease-in-out infinite;
}

.dot--1 {
  animation-delay: 0s;
}

.dot--2 {
  animation-delay: 0.1s;
}

.dot--3 {
  animation-delay: 0.2s;
}

@keyframes bounce {

  0%,
  100% {
    transform: translateY(0);
  }

  50% {
    transform: translateY(-6px);
  }
}

.mobile-transfer__p2p {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding-top: 12px;
  border-top: 2px dashed rgba(17, 17, 17, 0.1);
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

/* ---------- Mobile Transfer Actions ---------- */
.mobile-transfer__actions {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
  padding: 24px;
  padding-bottom: 48px;
}

.mobile-transfer__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 16px 24px;
  border-radius: var(--radius-lg);
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  font-weight: 700;
  box-shadow: var(--shadow-lego);
  transition: all 0.15s ease;
  cursor: pointer;
  font-family: var(--font-display);
  font-size: 1rem;
}

.mobile-transfer__btn:active {
  transform: translate(4px, 4px);
  box-shadow: none;
}

.mobile-transfer__btn--cancel:hover {
  background: #fef2f2;
}

/* ---------- Mobile Waiting ---------- */
.mobile-waiting {
  flex-direction: column;
  min-height: 100vh;
  background: var(--primary);
}

.mobile-waiting__body {
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 24px;
  align-items: center;
  justify-content: center;
  gap: 32px;
}

/* ---------- Mobile Incoming Request ---------- */
.mobile-incoming {
  flex-direction: column;
  align-items: center;
  padding: 48px 24px 32px;
  gap: 16px;
}

.mobile-incoming__device-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: #f3f4f6;
  border: 3px solid var(--neutral-dark);
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: var(--shadow-lego);
  margin-bottom: 8px;
}

.mobile-incoming__device-name {
  font-size: 1.5rem;
  font-weight: 700;
  text-transform: uppercase;
}

.mobile-incoming__network-badge {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 0.75rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  background: var(--primary);
  padding: 4px 12px;
  border-radius: var(--radius-full);
  border: 1px solid var(--neutral-dark);
  margin-bottom: 24px;
}

.mobile-incoming__file-card {
  display: flex;
  align-items: center;
  gap: 16px;
  width: 100%;
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  border-radius: var(--radius-xl);
  padding: 16px;
  box-shadow: var(--shadow-lego-sm);
  margin-bottom: 24px;
}

.mobile-incoming__file-icon {
  width: 48px;
  height: 48px;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fef3c7;
  border-radius: var(--radius-lg);
  border: 2px solid var(--neutral-dark);
}

.mobile-incoming__file-info {
  flex: 1;
  min-width: 0;
}

.mobile-incoming__file-name {
  font-size: 1rem;
  font-weight: 700;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.mobile-incoming__file-meta {
  display: flex;
  align-items: center;
  gap: 8px;
  margin-top: 4px;
  font-size: 0.875rem;
  color: #6b7280;
}

.mobile-incoming__file-badge {
  background: var(--primary);
  padding: 1px 6px;
  border-radius: 2px;
  font-size: 0.625rem;
  font-weight: 900;
  letter-spacing: 0.05em;
  color: var(--neutral-dark);
}

.mobile-incoming__accept {
  display: flex;
  width: 100%;
  height: 56px;
  align-items: center;
  justify-content: center;
  gap: 12px;
  border-radius: var(--radius-xl);
  background: var(--primary);
  border: 2px solid var(--neutral-dark);
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  box-shadow: var(--shadow-lego);
  cursor: pointer;
  transition: all 0.15s ease;
  font-family: var(--font-display);
}

.mobile-incoming__accept:active {
  transform: translate(4px, 4px);
  box-shadow: none;
}

.mobile-incoming__decline {
  display: flex;
  width: 100%;
  height: 56px;
  align-items: center;
  justify-content: center;
  border-radius: var(--radius-xl);
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  font-size: 1rem;
  font-weight: 700;
  letter-spacing: 0.05em;
  box-shadow: var(--shadow-lego);
  cursor: pointer;
  transition: all 0.15s ease;
  font-family: var(--font-display);
}

.mobile-incoming__decline:active {
  transform: translate(4px, 4px);
  box-shadow: none;
}

.mobile-incoming__note {
  font-size: 0.75rem;
  font-weight: 500;
  opacity: 0.6;
  margin-top: 8px;
}

/* ---------- Incoming card mobile override ---------- */
@media (max-width: 768px) {
  .incoming-card {
    max-width: 100%;
    margin: 0;
    border-radius: 0;
    height: 100vh;
    min-height: 100vh;
    border: none;
    display: flex;
    flex-direction: column;
  }
}

/* ---------- Room Join Section ---------- */
.room-join-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 24px;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
}

.room-join-card {
  background: var(--surface);
  border: var(--border-thick);
  border-radius: var(--radius-xl);
  padding: 32px;
  box-shadow: var(--shadow-lego);
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  text-align: center;
  transition: all 0.2s ease;
}

.room-join-card:hover {
  transform: translateY(-4px);
  box-shadow: var(--shadow-lego-lg);
}

.room-join-card__icon {
  width: 64px;
  height: 64px;
  border-radius: 50%;
  background: var(--primary);
  border: var(--border-medium);
  display: flex;
  align-items: center;
  justify-content: center;
}

.room-join-card__icon .material-symbols-outlined {
  font-size: 32px;
  font-weight: 700;
}

.room-join-card__title {
  font-size: 1.25rem;
  font-weight: 900;
  text-transform: uppercase;
}

.room-join-card__desc {
  font-size: 0.875rem;
  font-weight: 500;
  opacity: 0.7;
  line-height: 1.5;
}

.room-join-input {
  width: 100%;
  padding: 12px 16px;
  border: var(--border-medium);
  border-radius: var(--radius-sm);
  font-size: 1rem;
  font-family: var(--font-display);
  font-weight: 600;
  background: var(--bg-light);
  letter-spacing: 0.05em;
  text-align: center;
  box-sizing: border-box;
}

.room-join-input:focus {
  outline: none;
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(242, 204, 13, 0.3);
}

.room-join-input::placeholder {
  opacity: 0.4;
  font-weight: 500;
}

/* ---------- How It Works ---------- */
.how-it-works {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.how-step {
  display: flex;
  align-items: flex-start;
  gap: 16px;
  background: var(--surface);
  border: var(--border-medium);
  border-radius: var(--radius-lg);
  padding: 20px;
  box-shadow: var(--shadow-lego-sm);
}

.how-step__num {
  width: 40px;
  height: 40px;
  flex-shrink: 0;
  border-radius: 50%;
  background: var(--primary);
  border: var(--border-medium);
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 900;
  font-size: 1.125rem;
}

.how-step__title {
  font-weight: 700;
  font-size: 1rem;
  margin-bottom: 4px;
}

.how-step__desc {
  font-size: 0.875rem;
  opacity: 0.7;
  line-height: 1.5;
}

/* ---------- Mobile Join Form ---------- */
.mobile-join-form {
  display: flex;
  width: 100%;
  gap: 8px;
}

.mobile-join-form__input {
  flex: 1;
  padding: 14px 16px;
  border: 2px solid var(--neutral-dark);
  border-radius: var(--radius-xl);
  font-size: 1rem;
  font-family: var(--font-display);
  font-weight: 600;
  background: var(--surface);
  box-sizing: border-box;
}

.mobile-join-form__input:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(242, 204, 13, 0.3);
}

.mobile-join-form__input::placeholder {
  opacity: 0.4;
  font-weight: 500;
}

.mobile-join-form__btn {
  display: flex;
  align-items: center;
  gap: 6px;
  padding: 14px 20px;
  border-radius: var(--radius-xl);
  background: var(--surface);
  border: 2px solid var(--neutral-dark);
  font-size: 1rem;
  font-weight: 700;
  box-shadow: var(--shadow-lego-sm);
  cursor: pointer;
  transition: all 0.15s ease;
  font-family: var(--font-display);
}

.mobile-join-form__btn:active {
  transform: translate(2px, 2px);
  box-shadow: none;
}

.mobile-join-form__btn:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .room-join-section {
    grid-template-columns: 1fr;
  }
}